import Vue from 'vue'
import VueRouter from 'vue-router'
// import Home from '../views/Home.vue'
const Homes=()=>import('@/views/Home')
Vue.use(VueRouter)

  const routes = [
    {
      path: '/',
      name: 'Home',
      component: Homes
    },
 
]

const router = new VueRouter({
  routes
})

export default router



